import fetch from "isomorphic-unfetch";

const Redirect = props => {
  console.log(props);
  return <h1>hey</h1>;
};

Redirect.getInitialProps = async ({ url }) => {
  const apiLink = `http://localhost:4000/redirect/:${url}`;
  const res = await fetch(apiLink);
  const data = await res.json();
  console.log(data);
  return { data };
};

export default Redirect;
